@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lexend:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Aclonica&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@400;600&display=swap);
* {margin: 0;padding: 0;}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{
    --padding-sidebar: 20px;
    --sidebar-nav-height: 60%;
    --wrapper_background-color: #252836;
    --sidebar-background-color: #1F1D2B;
}

.wrapper{
    max-height: 100vh;
    background-color: #252836;
    background-color: var(--wrapper_background-color);
    overflow: hidden;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}
.sidebar-router{
    display: -webkit-flex;
    display: flex;
    height: calc(100vh - 20px * 2);
    height: calc(100vh - var(--padding-sidebar) * 2);
    padding: 20px;
    padding: var(--padding-sidebar);

    background-color: #1F1D2B;

    background-color: var(--sidebar-background-color);
    -webkit-flex-flow: column nowrap;
            flex-flow: column nowrap;

    -webkit-justify-content: space-between;

            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}
.sidebar-router .sidebar-router-logo{
    border-radius: 8px;
    width: 56px;
    height: 56px;
    background-color: rgba(235, 150, 106, .2);

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.sidebar-router .sidebar-router-logo li{

}
.sidebar-router .sidebar-router-nav{
    height: 60%;
    height: var(--sidebar-nav-height);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column nowrap;
            flex-flow: column nowrap;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-content: space-between;
            align-content: space-between;
}
.sidebar-router .sidebar-router-sign-out{

}
.sidebar-router li{
    list-style-type: none;
}
p{color: white}
.home{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row nowrap;
            flex-flow: row nowrap;
}

/* ************ menu_page ************ */

.menu_page{
    --menu-page-padding: 20px;
    --menu-page-header-left-flex-basis: 70%;
    --menu-page-header-right-flex-basis: calc(100% - var(--menu-page-header-left-flex-basis));

    --header-padding: 20px;
    --header-margin-bottom: 20px;
    --header-min-height: 100px;
    --header-height: calc( var(--header-min-height) - var(--header-padding)*2 );

    --menu-list-height: 50px;

    --menu-top-height: 50px;
}

.menu_page{
    -webkit-flex-basis: 70%;
            flex-basis: 70%;
    padding: var(--menu-page-padding);
    padding-bottom: 0;
    max-height: 100vh;
}

/* ************ Order ************ */

.order{
    -webkit-flex-basis: 30%;
            flex-basis: 30%;
    background-color: #1F1D2B;
    min-width: 30%;
}
.order p{
    color: white;
}
header{

}
header{
    display: -webkit-flex;
    display: flex;
    width: 100%;
    min-height: var(--header-height);
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-content: space-between;
            align-content: space-between;
    margin-bottom: var(--header-margin-bottom);
}
header .header-data-info{
    -webkit-flex-basis: var(--menu-page-header-left-flex-basis);
            flex-basis: var(--menu-page-header-left-flex-basis);

    display: -webkit-flex;

    display: flex;
    -webkit-flex-flow: column nowrap;
            flex-flow: column nowrap;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: flex-start;
            align-items: flex-start;
}
header .header-input-search{
    -webkit-flex-basis: var(--menu-page-header-right-flex-basis);
            flex-basis: var(--menu-page-header-right-flex-basis);

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.header-input-search input{
    border: 1px solid #393C49;
    border-radius: 10px;
    width: 220px;
    height: 48px;
    padding-left: 40px;
    color: white;
    font-family: "Lexend", sans-serif;

    background: #2D303E url(data:image/png;base64,) no-repeat 10px center;
    background-size: 20px;
}
input:focus{
    outline: none;
}
header p{
    color: white;
    font-family: "Barlow", sans-serif;
}
header p:first-child{
    font-size: 28px;
    font-weight: 600;
}
header p:last-child{
    font-size: 16px;
    font-weight: 400;
}
.item{
    --img-height: 130px;
    --img-width: 130px;
    --item-height: 260px;
    --item-margin-top: calc( var(--img-height) / 2 + 10px );
}

.item{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: flex-start;
            align-content: flex-start;


    margin: var(--item-margin-top) 10px calc( var(--item-margin-top) / 2);
    background: #1F1D2B;
    border-radius: 15px;
    width: 190px;
    height: var(--item-height);
}
.description{
    height: calc( var(--item-height) - (var(--img-height) / 2) - 10px );
    -webkit-transform: translateY( calc(
            var(--img-height) / 2 * -1
    ));
            transform: translateY( calc(
            var(--img-height) / 2 * -1
    ));

    display: -webkit-flex;

    display: flex;
    -webkit-flex-flow: column nowrap;
            flex-flow: column nowrap;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-flex-basis: 100%;
            flex-basis: 100%;
}
img{
    width: var(--img-width);
    height: var(--img-height);
    -webkit-transform: translateY( calc( var(--img-height) / 2 * -1) );
            transform: translateY( calc( var(--img-height) / 2 * -1) );
}
p{
    text-align: center;
    font-family: 'Aclonica', sans-serif;
    font-size: 20px;
}
p:last-child{
    color: gray;
}
/*......Menu_List......*/
.menu{

}
.menu{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column nowrap;
            flex-flow: column nowrap;
    font-family: 'Barlow', sans-serif;
    font-weight: 600;
}
.menu .menu_list{
    width: auto;
    height: var(--menu-list-height);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    overflow: auto;
}
.menu .menu_list ul{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    margin: 10px 0;
}
.menu .menu_list li{
    color: white;
    list-style-type: none;
    padding-right: 25px;
    cursor: pointer;
    -webkit-tap-highlight-color: #00000000;
    white-space: nowrap;
}
.status_line_parent{
    background-color: gray;
    border-radius: 5px;
}
.status_line_child{
    height: 3px;
    background-color: #EA7C69;
    border-radius: 5px;

    transition: 1500ms;
    transition-timing-function: ease;
}
.status_1{
    width: 75px;
}
.status_2{
    width: 100px;
    -webkit-transform: translateX(90px);
            transform: translateX(90px);
}
.status_3{
    width: 40px;
    -webkit-transform: translateX(205px);
            transform: translateX(205px);
}
.status_4{
    width: 40px;
    -webkit-transform: translateX(260px);
            transform: translateX(260px);
}
.status_5{
    width: 70px;
    -webkit-transform: translateX(320px);
            transform: translateX(320px);
}
.status_6{
    width: 60px;
    -webkit-transform: translateX(410px);
            transform: translateX(410px);
}


/*.....menu_grid.....*/

.menu_grid .menu_top{
    height: var(--menu-top-height);

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-flow: row nowrap;
            flex-flow: row nowrap;
    -webkit-align-items: center;
            align-items: center;
}
.menu_grid .menu_top button{
    height: 100%;
    grid-gap: 10px;
    gap: 10px;

    display: -webkit-flex;

    display: flex;
    -webkit-align-items: center;
            align-items: center;

    padding: 14px;
    color: white;
    background: #1F1D2B;
    border: 1px solid #393C49;
    box-sizing: border-box;
    border-radius: 8px;
}
button:focus{
    outline: none;
}
.menu .menu_grid{
    height: calc( 100vh - ( var(--header-height) + var(--menu-page-padding) + var(--menu-list-height) + var(--header-margin-bottom) ));
    overflow: scroll;
}
.menu .menu_grid_items{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}
.menu_grid_items::-webkit-scrollbar{
    display: none;
}
