@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@400;600&display=swap');
@import url("https://fonts.googleapis.com/css2?family=Lexend:wght@600&display=swap");

header{

}
header{
    display: flex;
    width: 100%;
    min-height: var(--header-height);
    justify-content: space-between;
    align-content: space-between;
    margin-bottom: var(--header-margin-bottom);
}
header .header-data-info{
    flex-basis: var(--menu-page-header-left-flex-basis);

    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    align-items: flex-start;
}
header .header-input-search{
    flex-basis: var(--menu-page-header-right-flex-basis);

    display: flex;
    justify-content: center;
    align-items: center;
}
.header-input-search input{
    border: 1px solid #393C49;
    border-radius: 10px;
    width: 220px;
    height: 48px;
    padding-left: 40px;
    color: white;
    font-family: "Lexend", sans-serif;

    background: #2D303E url(../../../assets/img/search.png) no-repeat 10px center;
    background-size: 20px;
}
input:focus{
    outline: none;
}
header p{
    color: white;
    font-family: "Barlow", sans-serif;
}
header p:first-child{
    font-size: 28px;
    font-weight: 600;
}
header p:last-child{
    font-size: 16px;
    font-weight: 400;
}